header {
  display: grid;
  grid-template-columns: 0.2fr 1fr;
  background-color: rgb(0, 0, 0);
  padding: 20px;
  padding-left: 50px;
  padding-right: 50px;
}

nav {
  display: grid;
  grid-template-columns: 2fr 0.2fr;
  justify-items: end;
  align-items: center;
}

.logo {
  color: white;
}

header .logo_img_itech {
  height: 70px;
  width: auto;
}

.nav_item {
  color: #001aff;
  font-size: 30px;
  font-weight: bold;
  cursor: pointer;
  text-decoration: none;
  padding: 20px;
  border: none;
  background: none;
}

.nav_item:hover {
  color: white;
}

.button_cart_item {
  border: none;
  background: none;
  cursor: pointer;
}

.cart_icon_header {
  display: grid;
  grid-template-columns: 1fr 0.5fr;
}

.cart_img_header {
  filter: invert(1);
}

.cart_count_header {
  align-self: end;
  /*bottom: -1px;*/
  /*right: -20px;*/
  margin-left: -7px;
  width: 23px;
  height: 23px;
  background: #0e8ce4;
  border-radius: 50%;
  text-align: center;
}

.cart_count_header span {
  line-height: 23px;
  font-size: 12px;
  color: #ffffff;
}
