/* Корзина */

.overlay {
  position: absolute;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.18);
  z-index: 1;
}

.cart {
  display: grid;
  grid-template-rows: 0.5fr 6fr 1fr;
  position: absolute;
  width: 700px;
  right: 0;
  height: 100%;
  background-color: rgb(255, 255, 255);
  box-shadow: -10px 0px 20px rgba(0, 0, 0, 0.38);
  padding: 0px 50px 0px 50px;
}

.title_block {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.title_block h2 {
  font-size: 32px;
}

.close_btn {
  width: fit-content;
  justify-self: end;
  align-self: center;
  padding: 10px 12px 10px 12px;
  background-color: white;
  color: #9d9d9d;
  border: 1px solid gray;
  border-radius: 12px;
  cursor: pointer;
}

.cart_list {
  overflow: auto;
}

.cart_item {
  display: grid;
  grid-template-columns: 1fr 3fr 1fr;
  border: 1px solid #9d9d9d;
  border-radius: 15px;
  padding: 15px;
  margin-bottom: 30px;
}

.cart_img {
  width: 70px;
}

.total_price {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1.3fr 2fr;
}

.total_price button {
  grid-row: 2/3;
  grid-column: 1/3;
  align-self: center;
  height: fit-content;
  background-color: #43ace6;
  border: 0;
  border-radius: 10px;
  color: white;
  font-size: 22px;
  padding: 20px;
  cursor: pointer;
  transition: background-color 0.2s linear, color 0.2s linear;
}

.total_price button:active {
  background-color: white;
  color: #43ace6;
}

.total_price p {
  align-self: end;
  font-size: 20px;
  font-weight: bold;
}

.total_price_summ {
  justify-self: end;
}
