.banner_section {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
  margin: 50px;
}

.banner {
  display: grid;
  grid-template-columns: 0.7fr 1fr;
  justify-items: end;
  align-items: center;
  background-image: url("/public/img/banner.png");

  background-size: cover;
  background-position: center;
  padding-top: 40px;
  padding-bottom: 40px;
  border-radius: 30px;
}

.text_banner {
  padding-top: 20px;
  color: black;
  font-size: 40px;
  font-weight: bold;
}

.text_banner span {
  color: #001aff;
}

.banner_btn {
  background-color: #001aff;
  color: white;
  margin-top: 20px;
  padding: 20px;
  padding-right: 25px;
  padding-left: 25px;
  font-size: 20px;
  font-weight: bold;
  border-radius: 20px;
  border: none;
  cursor: pointer;
  transition: box-shadow 0.4s ease-in-out, background-color 0.3s ease-in,
    color 0.5s ease-in;
}

.banner_btn:hover {
  box-shadow: 5px 5px 5px 0 black;
}

.banner_btn:active {
  background-color: white;
  color: #001aff;
}
