.product_section {
  max-width: 1200px;
  margin: 0 auto;
}

.product_section h2 {
  text-align: center;
}

.products {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-items: center;
}

.product_img {
  width: 200px;
  justify-self: center;
}

.product_title {
  font-size: 20px;
  font-weight: bold;
}

.price {
  padding-top: 10px;
  color: #9d9d9d;
  font-weight: bold;
}

.product_item {
  display: grid;
  border: 1px solid #9f9f9f;
  border-radius: 35px;
  padding: 20px;
  padding-bottom: 25px;
  margin: 20px;
  transition: box-shadow 0.5s ease-out, background-color 0.8s ease-in;
}

.product_item:hover {
  background-color: rgba(159, 159, 159, 0.1);
  box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.5);
}

.product_item p {
  margin-bottom: 5px;
  margin-top: 5px;
}

.product_price {
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-items: center;
  align-items: center;
}

.product_price span {
  font-weight: bold;
  font-size: 25px;
}

.search {
  display: grid;
  grid-template-columns: 2fr 1fr;
  align-items: center;
}

.search h2 {
  justify-self: start;
  font-size: 32px;
}

.search_block {
  justify-self: end;
  border: 1px solid #9f9f9f;
  border-radius: 14px;
  padding: 15px;
  width: 250px;
  transition: box-shadow 0.6s ease-out;
}

.search_block:hover {
  box-shadow: -1px 1px 15px 4px rgba(159, 159, 159, 0.24);
}

.search_block img {
  width: 13px;
  margin-right: 15px;
}

.search_block input {
  border: none;
  font-size: 16px;
}

.search_block input:focus {
  outline: none;
  border-bottom: 1px solid #9d9d9d;
}

.favorite_btn {
  border: 0;
  border-radius: 10px;
  font-size: 14px;
  background-color: #4fa4db;
  color: white;
  cursor: pointer;
  padding: 7px;
  text-align: start;
  width: fit-content;
  transition: transform 0.5s ease-in-out;
}

.favorite_btn:hover {
  transform: translateX(20px);
  box-shadow: -1px 1px 15px 4px rgba(45, 106, 175, 0.23);
  background-color: rgb(79, 164, 219);
}

.favorite_btn_added {
  border: none;
  border-radius: 10px;
  font-size: 14px;
  background-color: #2e6eb5;
  color: white;
  cursor: pointer;
  padding: 7px;
  text-align: start;
  width: fit-content;
  transition: background-color 0.5s ease-in-out, color 0.5s ease-in-out;
  box-shadow: -1px 1px 15px 4px rgba(45, 106, 175, 0.23);
}

.add_cart {
  justify-self: end;
  background-color: rgb(255, 255, 255);
  /*border: 1px solid rgb(159, 159, 159);*/
  border: none;
  border-radius: 50%;
  padding: 0;
  cursor: pointer;
  width: 40px;
  height: 40px;
  transition: box-shadow 0.6s ease-out, background-color 0.6s ease-out;
}

