.plus_btn {
  justify-self: end;
  background-color: rgb(255, 255, 255);
  /*border: 1px solid rgb(159, 159, 159);*/
  border: none;
  padding: 0;
  cursor: pointer;
  width: 50px;
  height: 50px;
}

.plus_btn img {
  width: 60px;
  transition: width 0.3s ease-in-out;
}

.check_btn {
  justify-self: end;
  background-color: rgb(255, 255, 255);
  /*border: 1px solid rgb(159, 159, 159);*/
  border: none;
  border-radius: 50%;
  padding: 0;
  cursor: pointer;
  width: 50px;
  height: 50px;
  transition: box-shadow 0.6s ease-out, background-color 0.6s ease-out;
}

.check_btn img {
  width: 40px;
  transition: width 0.4s ease-out;
}

.check_btn:hover {
  box-shadow: -1px 1px 15px 4px rgba(159, 159, 159, 0.24);
  background-color: rgba(159, 159, 159, 0.1);
}
